import { useState } from 'react';
import styled from 'styled-components';
import ThemeToggle from '../shared/ThemeToggle';

function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
      setIsMenuOpen(false);
    }
  };

  return (
    <Nav>
      <Logo onClick={() => scrollToSection('home')}>
        <span>{'<'}</span>YourName<span>{'/>'}</span>
      </Logo>

      <HamburgerButton onClick={() => setIsMenuOpen(!isMenuOpen)}>
        <span></span>
        <span></span>
        <span></span>
      </HamburgerButton>

      <NavLinks isOpen={isMenuOpen}>
        <NavItem onClick={() => scrollToSection('home')}>Home</NavItem>
        <NavItem onClick={() => scrollToSection('about')}>About</NavItem>
        <NavItem onClick={() => scrollToSection('projects')}>Projects</NavItem>
        <NavItem onClick={() => scrollToSection('skills')}>Skills</NavItem>
        <NavItem onClick={() => scrollToSection('contact')}>Contact</NavItem>
      </NavLinks>

      <ThemeToggle />
    </Nav>
  );
}

const Nav = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  background: rgba(30, 58, 95, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 10px rgba(38, 123, 142, 0.1);
  z-index: 1000;

  @media (max-width: ${({ theme }) => theme.breakpoints.md}) {
    padding: 0 25px;
  }
`;

const Logo = styled.div`
  color: ${({ theme }) => theme.colors.textLight};
  font-size: 1.5rem;
  font-weight: 700;
  cursor: pointer;
  
  span {
    color: ${({ theme }) => theme.colors.secondary};
  }
`;

const HamburgerButton = styled.button`
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 25px;
  background: transparent;
  border: none;
  cursor: pointer;
  z-index: 1100;

  span {
    width: 100%;
    height: 2px;
    background: ${({ theme }) => theme.colors.secondary};
    transition: all 0.3s ease;
  }

  @media (max-width: ${({ theme }) => theme.breakpoints.md}) {
    display: flex;
  }
`;

const NavLinks = styled.div`
  display: flex;
  gap: 2rem;

  @media (max-width: ${({ theme }) => theme.breakpoints.md}) {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 75%;
    max-width: 300px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: ${({ theme }) => theme.colors.primary};
    transform: ${({ isOpen }) => isOpen ? 'translateX(0)' : 'translateX(100%)'};
    transition: transform 0.3s ease;
    box-shadow: -10px 0px 30px -15px rgba(2, 12, 27, 0.7);
  }
`;

const NavItem = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-size: 1rem;
  cursor: pointer;
  transition: color 0.3s ease;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -5px;
    left: 0;
    background-color: ${({ theme }) => theme.colors.accent};
    transition: width 0.3s ease;
  }

  &:hover {
    color: ${({ theme }) => theme.colors.textLight};
    
    &:after {
      width: 100%;
    }
  }
`;

export default Navbar;
