import styled from 'styled-components';
import { motion } from 'framer-motion';
import { PrimaryButton, OutlinedButton } from '../shared/Buttons';

function ProjectCard({ project }) {
  return (
    <Card
      as={motion.div}
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
    >
      <ImageContainer>
        <ProjectImage src={project.image} alt={project.title} />
      </ImageContainer>
      
      <Content>
        <Title>{project.title}</Title>
        <Description>{project.description}</Description>
        
        <TechStack>
          {project.technologies.map((tech, index) => (
            <TechTag key={index}>{tech}</TechTag>
          ))}
        </TechStack>
        
        <ButtonGroup>
          <OutlinedButton as="a" href={project.github} target="_blank" rel="noopener noreferrer">
            <i className="fab fa-github" />
            <span>Code</span>
          </OutlinedButton>
          <PrimaryButton as="a" href={project.live} target="_blank" rel="noopener noreferrer">
            <span>Live Demo</span>
            <i className="fas fa-external-link-alt" />
          </PrimaryButton>
        </ButtonGroup>
      </Content>
    </Card>
  );
}

const Card = styled.article`
  background: ${({ theme }) => theme.colors.card};
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;

  &:hover {
    transform: translateY(-5px);
  }
`;

const ImageContainer = styled.div`
  width: 100%;
  height: 200px;
  overflow: hidden;
`;

const ProjectImage = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
`;

const Content = styled.div`
  padding: 1.5rem;
`;

const Title = styled.h3`
  color: ${({ theme }) => theme.colors.textLight};
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
`;

const Description = styled.p`
  color: ${({ theme }) => theme.colors.text};
  font-size: 0.875rem;
  line-height: 1.6;
  margin-bottom: 1rem;
`;

const TechStack = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
`;

const TechTag = styled.span`
  background: ${({ theme }) => theme.colors.primary}20;
  color: ${({ theme }) => theme.colors.secondary};
  padding: 0.25rem 0.75rem;
  border-radius: 50px;
  font-size: 0.75rem;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 1rem;
`;

export default ProjectCard;
