import styled from 'styled-components';
import ProjectCard from '../components/projects/ProjectCard';

const projectsData = [
  {
    title: "Project One",
    description: "A web application that helps users track their daily tasks and manage their time effectively.",
    image: "https://via.placeholder.com/600x340",
    technologies: ["React", "Node.js", "MongoDB"],
    github: "https://github.com",
    live: "https://example.com"
  },
  {
    title: "Project Two",
    description: "An e-commerce platform built with modern web technologies and best practices.",
    image: "https://via.placeholder.com/600x340",
    technologies: ["Next.js", "TypeScript", "Stripe"],
    github: "https://github.com",
    live: "https://example.com"
  },
  {
    title: "Project Three",
    description: "A real-time chat application with features like group messaging and file sharing.",
    image: "https://via.placeholder.com/600x340",
    technologies: ["React", "Firebase", "Material-UI"],
    github: "https://github.com",
    live: "https://example.com"
  }
];

function Projects() {
  return (
    <ProjectsSection>
      <SectionTitle>Featured Projects</SectionTitle>
      <ProjectsGrid>
        {projectsData.map((project, index) => (
          <ProjectCard key={index} project={project} />
        ))}
      </ProjectsGrid>
    </ProjectsSection>
  );
}

const ProjectsSection = styled.section`
  padding: 100px 150px;
  background: ${({ theme }) => theme.colors.background};

  @media (max-width: ${({ theme }) => theme.breakpoints.tablet}) {
    padding: 60px 50px;
  }
`;

const SectionTitle = styled.h2`
  color: ${({ theme }) => theme.colors.textLight};
  font-size: clamp(26px, 5vw, 32px);
  margin-bottom: 40px;

  &:after {
    content: '';
    display: block;
    width: 100px;
    height: 2px;
    background: ${({ theme }) => theme.colors.secondary};
    margin-top: 10px;
  }
`;

const ProjectsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
`;

export default Projects;
